.mainBlock {
    min-height: 100vh;
    background-color: #ff00fb;
}
.container {
    border: 1px solid;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.text {
    width: 300px;
    background-color: bisque;
}

.photo {
    width: 300px;
    height: 400px;
    background-color: bisque;

}